<template>
  <header>
    <dit-lang-icon :lang="item.lang"></dit-lang-icon>
    <div class="title" v-html="wbrTitle"></div>
    <wbr/>
    <speech :text="item.text" :lang="item.lang" :autospeak="autospeak" hotkey="Alt+S"></speech>
    <em v-if="item.transcription" class="transcription">{{ item.transcription }}</em>
  </header>
</template>

<script>
import Speech from '@/components/Speech/Speech';
import DitLangIcon from '@/components/DitLangIcon/DitLangIcon';

export default {
  name: 'Article Title',
  props: { item: Object, title: String, autospeak: [String, Boolean] },
  components: {
    DitLangIcon,
    Speech,
  },
  computed: {
    wbrTitle() {
      return (this.title || this.item.text).trim().split(/\s+/).join(' <wbr>');
    },
  },
};
</script>
