<template v-if="item">
  <div v-if="isCreation" class="crossword-loader">
    <div v-for="x in [1, 2, 3]" :key="x" :class="`loader-layout-${x}`">
      <template v-for="y in [1, 2, 3]" :key="y">
        <div v-for="z in [1, 2, 3]" :key="z" :class="`cube h${x} w${y} l${z}`">
          <div class="face top"></div>
          <div class="face left"></div>
          <div class="face right"></div>
        </div>
      </template>
    </div>
    <!-- for calcSize -->
    <div stype="visible: hidden" class="crossword-grid"><div class="col"></div></div>
  </div>
  <div v-else class="crossword">
    <div class="crossword-grid">
      <b-row v-for="(row, y) in matrix" :key="y">
        <b-col v-for="(cell, x) in row" :key="x">
          <input
            v-if="cell.isUsed"
            :class="`letter ${cell.items.map((item) => 'letter-' + item.id).join(' ')}`"
            :id="`letter-${cell.position.x}-${cell.position.y}`"
            value=""
            :defaultValue="cell.value.trim().toLowerCase()"
            :name="Math.random()"
            type="text"
            maxlength="1"
            @change="setSuccess(cell.items)"
            @click="onClick(cell.items, $event)"
            @keyup="onKeyUp(cell, $event)"
          />
        </b-col>
      </b-row>
    </div>
    <ul class="crossword-legend">
      <li
        v-for="(value, i) in usedItemsMap?.values()"
        :key="i"
        :class="`legend legend-${value.item.id}`"
      >
        <a class="nav-link" @click="onClick(value.item, {}, true)">{{
          value.item.translation
        }}</a>
      </li>
    </ul>
  </div>
</template>

<script>
import { build as buildCrossword } from '@/utils/crosswords-generator.js';

export default {
  name: 'Training By Crossword',
  inject: ['buttons'],
  props: { item: Object },
  data() {
    return {
      isCreation: false,
      size: { width: 15, height: 15 },
      minSize: { width: 7, height: 7 },
      length: 10,
      matrix: [],
      usedItemsMap: null,
      unSuccessIdSet: null,
      selectedItem: null,
    };
  },
  watch: {
    item() {
      this.update();
    },
    '$store.getters.alert'(alert) {
      if (alert?.name !== 'noneCrossword') return;
      this.buttons.set({
        left: {
          visible: true,
        },
        right: {
          visible: true,
        },
      });
    },
    isCreation(state) {
      this.buttons.set({
        left: {
          visible: !state,
        },
        right: {
          visible: !state,
        },
      });
    },
    selectedItem(item, lastItem) {
      if (item?.id === lastItem?.id) return;

      this.toogleClass(lastItem, 'active', false);
      this.toogleClass(item, 'active', true);
    },
    'unSuccessIdSet.size'(a, b) {
      this.$store.commit('success', b && !a);
    },
    'buttons.right.pressed'() {
      if (!this.$store.alert) return;
      this.exam();
    },
  },
  mounted() {
    this.update();
  },
  unmount() {
    this.$store.commit('alert', { noneCrossword: undefined });
  },
  methods: {
    calcSize() {
      // note: it's approximately
      this.size.width = Math.min(
        this.minSize.width,
        Math.floor(
          (2 * document.querySelector('.training-desk').clientWidth)
          /
          (3 * (document.querySelector('.crossword-grid .col')?.clientWidth || 48))
        )
      );
    },
    update() {
      this.$store.commit('alert', { noneCrossword: undefined });
      this.$store.commit('loaded', { currentTraining: true });

      this.isCreation = true;
      this.selectedItem = null;

      this.toogleClass(null, 'active');
      this.toogleClass(null, 'success');

      this.calcSize();
      setTimeout(() => {
        console.time('create crossword');
        let { matrix, usedItemsMap } = buildCrossword({
          items: [this.item, ...this.$store.getters.bulkItems, ...this.$store.getters.items],
          wordField: 'text',
          size: this.size,
          length: this.length,
        }) || {};
        if (!matrix || usedItemsMap.length < 3) {
          this.$store.commit('alert', { noneCrossword: this.item });
          this.$store.commit('loaded', { currentTraining: true });
          return;
        }
        // cut empty
        const starts = [];
        matrix = matrix
          .map((row) => {
            starts.push(row.findIndex(({ value }) => !!value));
            return row.splice(0, row.findLastIndex(({ value }) => !!value) + 1);
          })
          .filter((row) => row.length);
        const startIndexCell = Math.min(...starts.filter((i) => i !== -1));
        matrix.map((row) => row.splice(0, startIndexCell));

        this.matrix = matrix;
        this.usedItemsMap = usedItemsMap; // { id: { item: {id...}, position: {x, y} } }
        this.unSuccessIdSet = new Set(usedItemsMap.keys());

        this.$store.commit('loaded', { currentTraining: true });
        this.isCreation = false;
         console.timeEnd('create crossword');
      }, 0);
    },
    onKeyUp(cell, e) {
      e.stopPropagation();
      // e.stopImmediatePropagation();
      const position = { x: cell.position.x, y: cell.position.y };
      if (e.altKey || e.ctrlKey || e.metaKey) return;

      const key = e.key.toLowerCase();
      if (key.length === 1) return this.goToNextLetter(cell, e);

      let letter = null;
      switch (key) {
        case 'arrowup':
          letter = this.getLetterUp(position) || this.getLetterLeft(position);
          break;
        case 'arrowleft':
          letter = this.getLetterLeft(position) || this.getLetterUp(position);
          break;
        case 'backspace':
        case 'arrowright':
          letter = this.getLetterRight(position) || this.getLetterDown(position);
          break;
        case 'arrowdown':
          letter = this.getLetterDown(position) || this.getLetterRight(position);
          break;
        default:
          return;
      }
      if (letter) return letter.click();

      this.goToNextLetter(cell, e);
    },
    getLetterLeft({ x, y }) {
      let letter = null;
      for (; --x >= 0; ) {
        letter = document.getElementById(`letter-${x}-${y}`);
        if (letter) return letter;
      }
    },
    getLetterUp({ x, y }) {
      let letter = null;
      for (; --y >= 0; ) {
        letter = document.getElementById(`letter-${x}-${y}`);
        if (letter) return letter;
      }
    },
    getLetterRight({ x, y }) {
      let letter = null;
      for (; ++x < this.size; ) {
        letter = document.getElementById(`letter-${x}-${y}`);
        if (letter) return letter;
      }
    },
    getLetterDown({ x, y }) {
      let letter = null;
      for (; ++y < this.size; ) {
        letter = document.getElementById(`letter-${x}-${y}`);
        if (letter) return letter;
      }
    },
    goToNextLetter({ items }, e) {
      const currentLetter = e.currentTarget;
      const currentId = this.selectedItem?.id;

      // find the next letter after the current for the selected if unSuccess
      if (currentId && !this.checkSuccess(this.selectedItem)) {
        let isCurrentPassed = false;
        const nextLetter = [
          ...document.querySelectorAll(`.letter-${currentId}`),
        ].find((letter) => {
          if (isCurrentPassed) return letter;
          isCurrentPassed = letter === currentLetter;
        });

        if (nextLetter) return nextLetter.click();
      }

      // find the next letter after the current for a cellmate if unSuccess
      if (
        items.find((item) => {
          if (item.id === currentId) return; // check before
          if (!this.checkSuccess(item)) {
            let isCurrentPassed = null;
            const nextLetter = [
              ...document.querySelectorAll(`.letter-${item.id}`),
            ].find((letter) => {
              if (isCurrentPassed) return letter;
              isCurrentPassed = letter === currentLetter;
            });

            if (!nextLetter) return;
            this.onClick(item);
            return true;
          }
        })
      ) return;

      // find a letter for an any unSuccess item
      if (
        [...this.unSuccessIdSet].find((id) => {
          if (id !== currentId) {
            this.onClick(this.usedItemsMap.get(id).item);
            return true;
          }
        })
      ) return;

      // all Success
      currentId && this.toogleClass(this.selectedItem, 'active', false);
      currentLetter.blur();
    },

    selectLetter(letter) {
      if (!letter) return;
      letter.select();
      this.scrollIntoViewIfNeeded(letter);
      return true;
    },

    scrollIntoViewIfNeeded(el) {
      if (!el) return;
      el[el['scrollIntoViewIfNeeded'] ? 'scrollIntoViewIfNeeded' : 'scrollIntoView']();
    },

    onClick(items, { currentTarget } = {}, scrollToLetter = false) {
      items = [items].flat();

      // choose new selectedItem
      let hasCurrent = false;
      let isSuccessCurrent = false;
      const currentId = this.selectedItem?.id;
      const unSuccessItems = items.filter((item) => {
        const isCurrent = currentId === item.id;
        if (isCurrent) hasCurrent = true;
        if (this.checkSuccess(item)) {
          if (isCurrent) isSuccessCurrent = true;
          return;
        }
        return !isCurrent;
      });
      const item = hasCurrent && !isSuccessCurrent
        ? this.selectedItem                 // selected has && unsuccess – don't change it
        : (unSuccessItems[0] || items[0]);  // new unselected item – unsucces || success

      currentTarget ||= document.querySelector(`.letter-${item.id}`);
      currentTarget.select();

      console.log(
        'click',
        [this.selectedItem?.text],
        [item?.text],
        items.map((i) => i?.text),
        unSuccessItems.map((i) => i?.text),
        [hasCurrent, isSuccessCurrent],
        // currentTarget,
      );

      if (item.id !== currentId) {
        this.selectedItem = item;
        if (!scrollToLetter)
         return this.scrollIntoViewIfNeeded(document.querySelector(`.legend-${item.id}`));
      }
      this.scrollIntoViewIfNeeded(currentTarget);
    },

    checkSuccess(item) {
      if (!item) return false;
      const text = [...document.querySelectorAll(`.letter-${item.id}`)].reduce(
        (text, input) => text + (input.value || ' '),
        ''
      );
      return text.toLowerCase() === item.text.toLowerCase();
    },

    setSuccess(items) {
      [items].flat().forEach((item) => {
        const isSuccess = this.checkSuccess(item);
        this.unSuccessIdSet[isSuccess ? 'delete' : 'add'](item.id);
        this.toogleClass(item, 'success', isSuccess);
      });
    },

    toogleClass(item, className, force) {
      const addIdClass = item ? `-${item.id}` : '';
      const { training, complexity } = this.$store.getters.settings;
      const hardComplexity = training.complexity.crossword === complexity.crossword.max;

      document
        .querySelectorAll(`
          .legend${addIdClass},
          .letter${addIdClass}
        `)
        .forEach((el) => {
          const specialForce =
            !hardComplexity &&
            className === 'success' &&
            el.nodeName === 'INPUT' &&
            el.value.trim().toLowerCase() === el.defaultValue; // ' ' == ''
          el.classList.toggle(`crossword-${className}-item`, force || specialForce);
        });
    },

    exam() {
      const { usedItemsMap, unSuccessIdSet } = this;
      usedItemsMap.forEach((i, id) => {
        usedItemsMap.get(id).item.score.exam({
          name: 'crossword',
          result: unSuccessIdSet.has(id) ? 'error' : 'success',
        });
      });
    },
  },
};
</script>
