function generateWordsMap({ items, wordField, length }) {
  // Map: 'letter' => [item, item, item]
  const map = new Map();

  items.forEach((item) => {
    const word = item[wordField];
    if (word.length > length) return;

    word.split('').forEach((letter) => {
      const lib = map.get(letter);
      map.set(letter, insertIntoLib(item, wordField, lib));
    });
  });

  return map;
}

function insertIntoLib(item, wordField, lib) {
  const cpLib = lib ? lib.concat() : [];
  if (cpLib.filter((w) => w === item[wordField]).length) return cpLib;
  cpLib.push(item);

  return shuffle(cpLib);
}

function shuffle(deck) {
  let randomizedDeck = [];
  let array = deck.slice();
  while (array.length !== 0) {
    let rIndex = Math.floor(array.length * Math.random());
    randomizedDeck.push(array[rIndex]);
    array.splice(rIndex, 1);
  }
  return randomizedDeck;
}

function getCellByPosition(matrix, position) {
  if (!matrix.length) return null;

  const { x, y } = position;
  if (y >= 0 && y < matrix.length && x >= 0 && x < matrix[0].length) {
    return matrix[y][x];
  } else {
    return null;
  }
}

function generateMatrix(width, height) {
  const matrix = [];
  for (let y = 0; y < height; y++) {
    matrix[y] = [];
    for (let x = 0; x < width; x++) {
      matrix[y][x] = {
        isUsed: false,
        value: '',
        items: [],
        wordsDirection: [],
        position: { x, y },
      };
    }
  }
  return matrix;
}

function generatePositionsByWidthAndHeight(startPosition, matrix) {
  return function (width, height) {
    const { x: startX, y: startY } = startPosition;
    const realHeight = height <= matrix.length ? height : matrix.length;
    const realWidth = width <= matrix[0].length ? width : matrix[0].length;
    const stack = [];

    for (let y = startY; y < realHeight - startY; y++) {
      for (let x = startX; x < realWidth - startX; x++) {
        stack.push({ x, y });
      }
    }
    return stack;
  };
}

function getAvailableStartPosition(matrix, wordLength) {
  const colLength = matrix.length;
  const rowLength = matrix[0].length;

  const rowAvailableLength = (rowLength - wordLength) + 1;
  const colAvailableLength = (colLength - wordLength) + 1;

  const rowAvailableArea = { width: rowAvailableLength, height: colLength };
  const colAvailableArea = { width: rowLength, height: colAvailableLength };
  const commonAvailableArea = { width: rowAvailableLength, height: colAvailableLength };

  const getPositions = generatePositionsByWidthAndHeight({ x: 0, y: 0 }, matrix);

  const commonAvailablePositions = getPositions(
    commonAvailableArea.width,
    commonAvailableArea.height
  ).map(({ x, y }) => ({ x, y, availableX: true, availableY: true }));

  const commonAvailablePositionsSet = new Set(commonAvailablePositions.map(p => `${p.x}${p.y}`));

  const rowAvailablePositions = getPositions(rowAvailableArea.width, rowAvailableArea.height)
    .filter(p => !commonAvailablePositionsSet.has(`${p.x}${p.y}`))
    .map(({ x, y }) => ({ x, y, availableX: true, availableY: false }));

  const colAvailablePositions = getPositions(colAvailableArea.width, colAvailableArea.height)
    .filter(p => !commonAvailablePositionsSet.has(`${p.x}${p.y}`))
    .map(({ x, y }) => ({ x, y, availableX: false, availableY: true }));

  const availablePositions = commonAvailablePositions.concat(rowAvailablePositions, colAvailablePositions);

  return availablePositions;
}

function getAvailableStartPositionByDistance(matrix, position, distance, wordLength) {
  const { x, y } = position;

  const maxX = matrix[0].length;
  const maxY = matrix.length;

  const isXOverflow = x - distance < 0 || ((x - distance) + wordLength - 1) >= maxX;
  const isYOverflow = y - distance < 0 || ((y - distance) + wordLength - 1) >= maxY;

  // row
  const positions = [];
  if (!isXOverflow) {
    const newX = x - distance;
    const newY = y;

    const existed = positions.find(({ x, y }) => x === newX && y === newY);

    if (existed) {
      existed.availableX = true;
    } else {
      positions.push({ x: newX, y: newY, availableX: true, availableY: false });
    }
  }
  // col
  if (!isYOverflow) {
    const newX = x;
    const newY = y - distance;
    const existed = positions.find(({ x, y }) => x === newX && y === newY);

    if (existed) {
      existed.availableY = true;
    } else {
      positions.push({ x: newX, y: newY, availableX: false, availableY: true });
    }
  }

  return positions;
}

function indexOfAll(array, searchElement) {
  const indexes = array.reduce((pre, cur, index) => {
    if (cur === searchElement) {
      pre.push(index);
    }
    return pre;
  }, []);
  return indexes.length ? indexes : -1;
}

function getLastElement(list) {
  if (!list?.length) return;
  return list.concat().pop();
}

function isArrayEqual(arr1, arr2) {
  return (
    JSON.stringify(arr1) === JSON.stringify(arr2) ||
    JSON.stringify(arr1.reverse()) === JSON.stringify(arr2)
  );
}

function fillFromStartPosition(
  { wordsMap, wordField, length },
  matrix,
  usedItemsMap,
  initPosition
) {
  let position = initPosition;
  let cell = getCellByPosition(matrix, position);
  const startWords = getLastElement(cell.items);
  const direction = getLastElement(cell.wordsDirection);

  let newMatrix = JSON.parse(JSON.stringify(matrix));
  const newUsedItemsArray = [];

  while (
    usedItemsMap.size < length &&
    cell &&
    getLastElement(cell.items)?.id === startWords.id
  ) {
    const { x, y } = position;
    const { value: letter } = cell;

    const items = wordsMap.get(letter);
    if (!items) throw new Error(`There is no words for letter "${letter}"`);

    for (const item of items) {
      if (usedItemsMap.has(item.id)) continue;

      let isFillSuccess = false;
      const word = item[wordField];
      if (word !== startWords && !usedItemsMap.has(item.id)) {
        // the words may be "TAA", thus the indexes of "A" is [1,2]
        const indexes = indexOfAll(word.split(''), letter);
        if (!indexes) throw new Error(`"${letter}" is not in "${word}"`);

        for (let index of indexes) {
          const distance = index;
          const availablePositions = getAvailableStartPositionByDistance(matrix, position, distance, word.length);

          for (let startPosition of availablePositions) {
            const filledMatrix = fillMatrix(newMatrix, startPosition, { item, wordField });

            if (filledMatrix) {
              isFillSuccess = true;
              newMatrix = filledMatrix;

              usedItemsMap.set(item.id, { item, position: startPosition });
              newUsedItemsArray.push({ item, position: startPosition });
              break;
            }
          }

          if (isFillSuccess) break;
        }
      }
      if (isFillSuccess) break;
    }

    // next words
    if (direction === 'row') {
      position = { x: x + 1, y };
    } else if (direction === 'col') {
      position = { x, y: y + 1 };
    } else {
      throw new Error('invalid cell direction');
    }

    cell = getCellByPosition(matrix, position);
  }

  return { newMatrix, newUsedItemsArray };
}

function fillMatrix(matrix, startPosition, { item, wordField }) {
  const word = item[wordField];
  const { x: startX, y: startY, availableX, availableY } = startPosition;
  const maxX = matrix[0].length;
  const maxY = matrix.length;

  let rowFillSuccess = true;
  let colFillSuccess = true;

  const newMatrix = JSON.parse(JSON.stringify((matrix)));
  if (availableX) {
    for (let x = startX, wIndex = 0; x < maxX && wIndex < word.length; x++, wIndex++) {
      const isStart = x === startX;
      const isEnd = x === maxX - 1 || wIndex === word.length - 1;
      const isFillSuccess = fillCell(
        newMatrix,
        { x, y: startY },
        {
          letter: word[wIndex],
          item,
          wordField,
        },
        'row',
        isStart,
        isEnd
      );

      if (!isFillSuccess) {
        rowFillSuccess = false;
        break;
      }
    }

    if (rowFillSuccess) {
      return newMatrix;
    }
  } else {
    rowFillSuccess = false;
  }

  if (availableY && !rowFillSuccess) {
    for (let y = startY, wIndex = 0; y < maxY && wIndex < word.length; y++, wIndex++) {
      const isStart = y === startY;
      const isEnd = y === maxY - 1 || wIndex === word.length - 1;
      const isFillSuccess = fillCell(
        newMatrix,
        { x: startX, y },
        {
          letter: word[wIndex],
          item,
          wordField,
        },
        'col',
        isStart,
        isEnd
      );

      if (!isFillSuccess) {
        colFillSuccess = false;
        break;
      }
    }

    if (colFillSuccess) {
      return newMatrix;
    }
  } else {
    colFillSuccess = false;
  }

  return rowFillSuccess || colFillSuccess;
}

function fillCell(
  matrix,
  position,
  { letter, item, wordField },
  direction,
  isStartCell = false,
  isEndCell = false
) {
  const { x, y } = position;
  const cell = matrix[y][x];
  const word = item[wordField];

  if (typeof cell.isUsed === "undefined") throw new Error('isUsed is not existed');

  if (cell.isUsed && cell.value !== letter) return false;

  if (cell.value === letter && (cell.wordsDirection[0] === direction || cell.wordsDirection[1] === direction)) {
    return false;
  }

  if (direction === 'row') {
    // up
    const upPosition = { x, y: y - 1 };
    const upCell = getCellByPosition(matrix, upPosition);
    if (!cell.isUsed && upCell && upCell.isUsed && !isArrayEqual(upCell.items, cell.items)) {
      return false;
    }

    // down
    const downPosition = { x, y: y + 1 };
    const downCell = getCellByPosition(matrix, downPosition);
    if (!cell.isUsed && downCell && downCell.isUsed && !isArrayEqual(downCell.items, cell.items)) {
      return false;
    }

    // right
    const rightPosition = { x: x + 1, y };
    const rightCell = getCellByPosition(matrix, rightPosition);
    if (isEndCell && rightCell && rightCell.isUsed) {
      return false;
    }
    if (!cell.isUsed && rightCell && rightCell.isUsed && rightCell.value !== letter) {
      return false;
    }

    // left
    const leftPosition = { x: x - 1, y };
    const leftCell = getCellByPosition(matrix, leftPosition);
    if (isStartCell && leftCell && leftCell.isUsed) {
      return false;
    }

  } else if (direction === 'col') {
    // left
    const leftPosition = { x: x - 1, y };
    const leftCell = getCellByPosition(matrix, leftPosition);
    if (!cell.isUsed && leftCell && leftCell.isUsed && !isArrayEqual(leftCell.items, cell.items)) {
      return false;
    }

    // right
    const rightPosition = { x: x + 1, y };
    const rightCell = getCellByPosition(matrix, rightPosition);
    if (!cell.isUsed && rightCell && rightCell.isUsed && !isArrayEqual(rightCell.items, cell.items)) {
      return false;
    }

    // down
    const downPosition = { x, y: y + 1 };
    const downCell = getCellByPosition(matrix, downPosition);
    if (isEndCell && downCell && downCell.isUsed) {
      return false;
    }
    if (!cell.isUsed && downCell && downCell.isUsed && downCell.value !== letter) {
      return false;
    }

    // up
    const upPosition = { x, y: y - 1 };
    const upCell = getCellByPosition(matrix, upPosition);
    if (isStartCell && upCell && upCell.isUsed) {
      return false;
    }
  }

  cell.value = letter;
  cell.items.push(item);
  cell.isUsed = true;
  cell.wordsDirection.push(direction);

  return true;
}

function getRandomELement(list) {
  if (!list?.length) return;
  return list[Math.floor(Math.random() * list.length)];
}

function init({ items, wordField }, matrix) {
  // init start words
  const startItem = items[0];
  const startWord = startItem[wordField];

  const availablePositions = getAvailableStartPosition(matrix, startWord.length);
  if (!availablePositions.length) return;
  const startPosition = getRandomELement(availablePositions);

  const startMatrix = fillMatrix(matrix, startPosition, { item: startItem, wordField });
  if (!startMatrix) throw new Error('init failed');

  return { startMatrix, startPosition, startItem };
}

function build(options) {
  if (!options.items.length) return {};
  const { length, wordField, size } = options;

  const initMatrix = generateMatrix(size.width || size, size.height || size); // [[[],[]],[[],[]],[[],[]]]
  const wordsMap = generateWordsMap(options);     // Map: 'letter' => [item, item, item]

  const { startMatrix, startPosition, startItem } = init(options, initMatrix) || {};
  if (!startItem) return;

  let matrix = startMatrix;

  const usedItemsMap = new Map();
  usedItemsMap.set(startItem.id, { item: startItem, position: startPosition });

  let usedWordsQueue = [];
  usedWordsQueue.push({ item: startItem, position: startPosition });

  while (usedWordsQueue.length !== 0) {
    const { position } = usedWordsQueue.shift();
    const { newMatrix, newUsedItemsArray } = fillFromStartPosition(
      { wordsMap, wordField, length },
      matrix,
      usedItemsMap,
      position
    );
    usedWordsQueue = usedWordsQueue.concat(newUsedItemsArray);
    matrix = newMatrix;
  }

  return { matrix, usedItemsMap };
}

module.exports = {
  build,
};
