<template>
  <b-button
    v-if="item.score.value || button.name === 'score'"
    :class="`btn-score ${this.class}`"
    variant="outline-secondary"
    :title="$t(`score.title.${button.name}`, item)"
    @click="toggleModal(button.name)"
  >
    <template v-if="button.name === 'clear'">
      <iconify-icon :icon="button.icon"></iconify-icon>
    </template>
    <template v-else>{{ item.score }}</template>
  </b-button>

  <b-modal
    v-model="show.score"
    class="score-info"
    :ok-only="!item.score.value"
    :ok-title="$t('buttons.close')"
    :cancel-title="$t('buttons.clear')"
    :title="item.text"
    @cancel="toggleModal('clear')"
    ref="score"
  >
    <dl class="score-info">
      <div
        class="score-info-item"
        v-for="type in item.score.info"
        :key="type.name"
      >
        <dt>{{ $t(`actions.title.${ type.name }`) }}</dt>
        <dd>{{ type.value }}</dd>
      </div>
      <div class="score-info-summary">
        <dd>{{ item.score }}</dd>
      </div>
    </dl>
  </b-modal>

  <!-- Clear-->
  <b-modal
    centered
    hideHeader
    v-model="show.clear"
    :ok-title="$t('buttons.clear')"
    :cancel-title="$t('buttons.keep')"
    @ok="clearScore(item)"
    ref="clear"
  >{{ $t('score.title.clear', item) }}</b-modal>

  <!-- Summary-->
  <b-modal
    centered
    :modalClass="`summary-modal summary-data-${summaryGoals}`"
    hideHeader
    ok-only
    :ok-title="$t('buttons.continue')"
    v-model="show.summary"
    ref="summary"
  >
    <div class="summary-icon"></div>
    <div class="summary-text">
      {{ $t('score.summary.done') }}
      <div class="summary-extra">{{
        $tc(
          `score.summary.extra[${summaryGoals < 4 ? summaryGoals : 4}]`,
          summaryGoals
        )
      }}</div>
    </div>
  </b-modal>
</template>

<script>
import { Icon as IconifyIcon } from '@iconify/vue';

export default {
  name: 'Score Info',
  components: {
    IconifyIcon,
  },
  props: {
    item: Object,
    button: Object,
    class: String,
  },
  data() {
    return {
      show: {
        score: false,
        clean: false,
        summary: false,
      },
    };
  },
  computed: {
    summaryGoals() { return  this.$store.getters.summaryGoals; },
  },
  watch: {
    summaryGoals() {
      this.toggleModal('summary');
    },
  },
  methods: {
    toggleModal(name) {
      this.show[name] = !this.show[name];
      if (!this.show[name]) return;

      window.setTimeout(() => {
        this.$refs[name].$refs.element.focus();
      }, 300);
    },
    clearScore(item) {
      (item || this.item).score.clear();
    },
  },
};
</script>
