import merge from 'deepmerge';
import { getIndexedDB } from '../utils/indexeddb';
import defaultSettings from '../views/Settings/settings.jsonc';
import { i18n } from '@/i18n';

export default {
  namespaced: false,
  state: {
    userID: 12345,          // todo: auth
    settings: null,         // user settings
    readyState: 0,          // for sync loaded
    indexedDB: null,
    voices: { langs: [] },  // enabled device voices
    network: true,
    saved: null,            // for set temporaty settings and restore
  },
  getters: {
    settings: (state) => state.settings,
    voices: (state) => state.voices,
    complexity: (state) => state.settings.complexity,
    network: (state) => state.network,
    isTempDictionary: (state) => !!state.saved,
  },
  mutations: {
    network(state, type) {
      state.network = type === 'online';
    },
    setSettings(state, { settings }) {
      if (state.settings && state.settings?.updated >= settings.updated) return;

      state.settings = settings;

      this.dispatch('syncSettingsDB');
    },
    setVoices(state) {
      if (state.voices.langs.length) return state.voices;

      const { langs } = this.getters;
      if (!langs.length) return state.voices;
      if (!speechSynthesis.getVoices().length) return;

      const voices = {};
      speechSynthesis.getVoices().forEach((voice) => {
        if (!voice.localService) return;

        const lang = voice.lang.split(/[-_]/)[0];
        if (langs.indexOf(lang) < 0) return;
        if (!voices[lang]) voices[lang] = [];
        state.settings.speak[lang] ||= {};

        const speakLangSettings = state.settings.speak[lang];
        if (voice.voiceURI === speakLangSettings.voiceURI)
          speakLangSettings.voice = voice;
        if (
          speakLangSettings.voice &&
          speakLangSettings.voice?.toString() !== '[object SpeechSynthesisVoice]'
        ) delete speakLangSettings.voice;

        voices[lang].push(voice);
      });

      state.settings.speak.readyState = 4;

      voices.langs = Object.keys(voices);
      voices.langs.forEach((lang) => {
        voices[lang].sort((a, b) => {
          return a.lang === b.lang ? 0 : (a.lang > b.lang ? 1 : -1);
        });
      });
      state.voices = voices;
    },
    setTempSettings(state, settings) {
      if (!settings) return;

      state.saved = {};
      Object.keys(settings).forEach((key) => {
        state.saved[key] = state.settings[key];
      });
      // state.saved = JSON.stringify(state.saved); // check: need to break links?

      state.settings = merge.all([{}, state.settings, settings]);
      state.settings.updated = 0;
    },
    clearTempSettings(state) {
      if (!state.saved) return;

      // state.settings = merge.all([{}, state.settings, JSON.parse(state.saved)]);
      state.settings = merge.all([{}, state.settings, state.saved]);
      state.saved = null;

      state.settings.training.collectionID = this.getters.collection.id;
      this.commit('collectionID', state.settings.training.collectionID);
    },
  },
  actions: {
    async fetchSettings({ commit, dispatch, state }) {
      if (state.readyState !== 0) return state;

      state.readyState = 1;
      const sets = [defaultSettings]; // [def, mongodb, indexeddb]
      let localSettings;
      return dispatch('fetchSettingsIdDB')
        .then(function (e) {
          localSettings = e.target.result;
          if (!localSettings) return Promise.reject(e);
          delete localSettings.extra;
          delete localSettings.updated;
          sets.push(localSettings);
        })
        .catch(function (error) {
          console.info('Only session default settings will be used.\n', error);
        })
        .finally(() => {
          const sort = (a, b) => a.updated === b.updated ? 0 :(a.updated > b.updated ? -1 : 1);
          let settings = {
            userID: state.userID,
          };
          Object.keys(defaultSettings).forEach((key) => {
            settings[key] = sets
              .map((set) => {
                return {
                  [key]: set[key],
                  updated: set[key]?.updated || set.updated,
                };
              })
              .sort(sort)[0][key];
          });
          settings = merge.all([{}, defaultSettings, settings], {
            arrayMerge: (a, b) => b,
          });

          if (settings.ui.locale) i18n.locale = settings.ui.locale;
          settings.ui.locale ||= i18n.locale;
          settings.training.native ||= i18n.locale;
          state.readyState = 4;
          commit('setSettings', { settings });
        });
    },
    async fetchSettingsIdDB({ state }) {
      state.indexedDB = getIndexedDB();
      return state.indexedDB.then(function (db) {
        return db.store('settings').get(state.userID);
      });
    },
    async syncSettingsDB({ state, commit }) {
      commit('collectionID', state.settings.training.collectionID);

      if (!state.indexedDB || !state.settings || state.isTempDictionary) return;

      return state.indexedDB.then((db) => {
        const updated = state.settings.updated;
        if (updated <= state.indexedDB.updated) return;

        const settings = JSON.parse(JSON.stringify(state.settings));
        delete settings.training.complexity; // todo: make a checkbox to save?
        settings.speak.readyState = 0;

        return db
          .store('settings')
          .set(settings)
          .then(() => {
            state.indexedDB.updated = updated;
          });
      });
    },
  },
};
