import qs from 'qs';
import axios from 'axios';
import merge from 'deepmerge';

export default {
  namespaced: false,
  state: {
    articleLangs: [],
    lookup: undefined,
    queryCorpus: undefined,
    transcription: '',
  },
  getters: {
    articleLangs: (state) => state.articleLangs,
    articleEnable:
      (state, getters) =>
      ({ native, lang }) =>
        getters.network && state.articleLangs.includes(`${lang}-${native}`),
    lookup: (state) => state.lookup,
    queryCorpus: (state) => state.queryCorpus,
  },
  mutations: {
    articleLangs: (state, langs) => {
      state.articleLangs = langs;
    },
    setArticle: (state, { lookup, queryCorpus, item }) => {
      state.lookup = lookup;
      state.queryCorpus = queryCorpus;
      item.transcription ??= lookup?.regular?.[0]?.ts || '';
    },
  },
  actions: {
    async fetchArticleLags({ state, commit, rootGetters }) {
      if (state.articleLangs.length) return;
      const {
        VUE_APP_DICT_BASE_URL,
        VUE_APP_DICT_LANGS,
        VUE_APP_YANDEX_DICT_API_KEY,
      } = rootGetters.env;
      return await axios({
        method: 'get',
        url: VUE_APP_DICT_LANGS,
        baseURL: VUE_APP_DICT_BASE_URL,
        params: {
          key: VUE_APP_YANDEX_DICT_API_KEY,
        },
      }).then((response) => {
        response.data.length && commit('articleLangs', response.data);
      });
    },
    async fetchArticle({ commit, rootGetters }, params) {
      if (!rootGetters.articleEnable(params)) {
        commit('alert', { noneArticle: true });
        return false;
      }
      const { lang, text, native } = params;
      const {
        VUE_APP_YANDEX_DICT_API_KEY,
        VUE_APP_DICT_BASE_URL,
        VUE_APP_DICT_LOOKUP_URL,
        VUE_APP_DICT_QUERYCORPUS_URL,
      } = rootGetters.env;
      const direct = params.lang + '-' + native;
      const dicts = this.getters.settings.extra[lang]?.dicts || [];
      const requestLookup = {
        url: VUE_APP_DICT_LOOKUP_URL,
        params: {
          lang: direct,
          ui: native,
          dict: [
            `${direct}.regular`,
            ...dicts.map((dict) => `${lang}.${dict}`),
          ].join(','),
          type: ['regular', ...dicts].join(','),
          text,
          key: VUE_APP_YANDEX_DICT_API_KEY,
        },
      };
      const urlQueryCorpus = {
        url: VUE_APP_DICT_QUERYCORPUS_URL,
        params: {
          lang: direct,
          ui: native,
          src: text,
        },
      };
      const doRequest = (data) => {
        return data.url
          ? {
              method: 'get',
              baseURL: VUE_APP_DICT_BASE_URL,
              url: data.url + qs.stringify(data.params),
              headers: {
                'Access-Control-Allow-Origin': VUE_APP_DICT_BASE_URL,
              },
            }
          : null;
      };

      return Promise.allSettled([
        axios(doRequest(requestLookup)),
        axios(doRequest(urlQueryCorpus)),
      ]).then((responses) => {
        const results = responses.map((result, i) => {
          if (result.status === 'rejected') {
            return {
              error: result.reason?.response?.data || result.reason,
            };
          }
          // QueryCorpus
          if (i === 1) return result.value.data.result;
          // Lookup
          let j = 0;
          const lookup = merge.all([
            result.value.data[direct] || {},
            result.value.data[lang] || {},
            { def: result.value.data.def || [] },
          ]);
          Object.keys(lookup).forEach((key) => !lookup[key].length ? delete lookup[key] : j++);

          return j ? lookup : null;
        });

        if (results.every((result) => result?.error?.code === 'ERR_NETWORK')) {
          commit('alert', { noneNetwork: { url: VUE_APP_DICT_BASE_URL } });
          return;
        }

        commit('setArticle', {
          lookup: results[0] || null,
          queryCorpus: results[1] || null,
          item: params,
        });

        return true;
      });
    },
  },
};
