<template>
  <article v-show="isShown" class="article">
    <b-row class="article-title">
      <b-col>
        <article-header :item="currentItem" autospeak/>
      </b-col>
    </b-row>
    <b-tabs
      v-model="active"
      content-class="row article-content"
      fill
      ref="tabs"
      @activate-tab="setOverflow"
    >
      <b-tab
        v-for="name in keys"
        :key="name"
        :class="`${name}-col`"
        :ref="name"
        :title-item-class="`${name}-title`"
      >
        <template #title>
          <h4>{{ $t(`article.${name}`) }}:</h4>
          <div
            v-if="name === 'query-corpus'"
            :title="$t('article.action.training-examples')"
            class="temp-dictionary-btn"
            @click="createTmpDictionary"
            role="button"
          >
            <iconify-icon icon="mdi:library-edit-outline"></iconify-icon>
          </div>
        </template>
        <component :is="name" @shown:article="onShown" :item="currentItem" />
      </b-tab>
    </b-tabs>
  </article>
</template>

<script>
import { Icon as IconifyIcon } from '@iconify/vue';
import ArticleHeader from '@/components/Article/Header';
import Lookup from '@/components/Article/Lookup';
import QueryCorpus from '@/components/Article/QueryCorpus';
import { getItem } from '@/utils/item.js';

export default {
  name: 'Dictionary Article',
  props: { item: Object },
  components: {
    IconifyIcon,
    ArticleHeader,
    Lookup,
    QueryCorpus,
  },
  data() {
    return {
      loaded: false,
      shown: {
        lookup: undefined,
        'query-corpus': undefined,
      },
      active: 0,
    };
  },
  computed: {
    keys() {
      return Object.keys(this.shown);
    },
    isLoaded() {
      return this.loaded &&
        (this.shown.lookup !== undefined
         || this.shown['query-corpus'] !== undefined);
    },
    isShown() {
      return Object.values(this.shown).some((val) => val);
    },
    currentItem() {
      return this.item || this.$route.params;
    },
  },
  watch: {
    isLoaded(isLoaded) {
      if (!isLoaded) return;

      if (this.isShown) {
        this.setActive();
        this.checkOverflow();
      } else {
        this.$store.commit('alert', { noneArticle: true });
      }
    },
    async currentItem() {
      this.$store.commit('alert', { noneArticle: undefined });
      await this.fetchData();

      this.keys.forEach((name) => {
        const el = document.querySelector(`.${name}-col`);
        if (el) el.scrollTop = 0;
      });
    },
  },
  async created() {
    await this.fetchData();
    this.$store.commit('success', true);
  },
  mounted() {
    this.$store.commit('loaded', { currentTraining: false });
  },
  unmounted() {
    this.$store.commit('alert', { noneArticle: undefined });
    this.$store.commit('loaded', { currentTraining: undefined });
  },
  methods: {
    async fetchData() {
      this.loaded = false;
      this.onShown({
        name: 'lookup',
        shown: undefined,
      });
      this.onShown({
        name: 'query-corpus',
        shown: undefined,
      });

      await this.$store.dispatch('fetchArticle', this.currentItem);
      this.$store.commit('loaded', { currentTraining: true });

      this.loaded = true;
    },
    onShown({ name, shown }) {
      this.shown[name] = shown;
      this.$el?.classList?.toggle(`${name}-shown`, !!shown);
    },
    checkOverflow() {
      setTimeout(() => {
        this.keys.some((name, i) => this.setOverflow(i));
      }, 500);
    },
    setOverflow(active = this.active) {
      const name = this.keys[active];
      const $el = this.$refs?.[name]?.[0]?.$el;
      setTimeout(() => {
        $el && $el.classList?.toggle('overflowed', $el.scrollHeight > $el.offsetHeight)
      }, 0); // toggling the show class spoils here
    },
    setActive() {
      this.keys.find((name, i) => {
        if (this.shown[name]) {
          this.active = i;
          return true;
        }
      });
    },
    createTmpDictionary(e) {
      e.preventDefault();
      const { lang, native } = this.currentItem;
      const collectionID = 'temp';
      const props = { collectionID, native, lang };
      const items = this.$store.getters.queryCorpus.examples.map((example) => {
        const [text, translation] = [example.src, example.dst].map((text) => {
          return text
            .replace(/''/, '"') // sodomia
            .replace(/--/, '-')
            .replace(/[><]/g, '');
        });
        const item = getItem({ text, translation }, props);
        this.$store.getters.unfoldStore(this.$store, item);
        return item;
      });

      this.$store.commit('setTempSettings', {
        training: {
          collectionID,
          count: null,
          lang,
          native,
          type: 'docking',
          textType: null,
          complexity: {
            docking: this.$store.getters.settings.complexity['docking'].min,
            writing: this.$store.getters.settings.complexity['writing'].min,
          },
        },
      });
      this.$store.commit('setItems', { items, dictionaryID: collectionID });
      this.$router.push('/docking');
    },
  },
};
</script>
