/**
  Used in AddDictionary and DictionaryStore
*/
let gid = Date.now();
export const getItem = (source, props = {}) => {
  const { native, lang, collectionID } = props;

  const direction = source.lang?.toLowerCase() || lang;
  const [from, to] = direction.split('-');
  const direct = from == native;

  const { id, translation, transcription, text } = source;

  return verifyItem(
    normalizeItem({
      id: id || gid++,
      collectionID: collectionID,
      lang: direct ? to : from,
      native: source.native || (direct ? from : to) || native,
      text: direct ? translation : text,
      translation: direct ? text : translation,
      transcription,
    })
  );
};

export const normalizeItem = (item) => {
  item.text = item.text?.trim()?.replace(/\s+/, ' ');
  item.translation = item.translation?.trim()?.replace(/\s+/, ' ');
  item.transcription &&= item.transcription?.trim()?.replace(/\s+/, ' ');
  item.lang = item.lang?.toLowerCase();
  item.native = item.native?.toLowerCase();
  item.native = item.native?.toLowerCase();
  item.textType = textType(item.text, item.lang);

  return item;
};

export const verifyItem = (item) => {
  if (!(item.lang && item.native && item.text && item.translation && item.collectionID)) return null;

  return item;
};

export const reverseItem = (item) => {
  [item.lang, item.native] = [item.native, item.lang];
  [item.text, item.translation] = [item.translation, item.text];

  [item.reversed, item.transcription] = [
    [item.transcription],
    item.reversed?.[0] || '',
  ];

  return item;
};

export const directItem = (item) => {
  if (!item.reversed) return item;

  reverseItem(item);
  delete item.reversed;

  return item;
};

export const toggleItem = (item) => {
  item.reversed ? directItem(item) : reverseItem(item);
};

  // define word, phrase, sentence
export const textType = (text, lang) => {
  //todo: more rules and languages
  if (lang === 'en') {
    text = text
      .replace(/(^|\s)(a|the|to)(\s)/gi, ' ')
      .replace(/(\w)'(s|ll|d|ve|m|re)(\s|$)/gi, '$1 $2$3')
      .trim();
  }
  const partsCount = text.split(/\s+/).length;
  return partsCount > 3 ? 'sentence' : partsCount > 1 ? 'phrase' : 'word';
};
